import {Component, OnInit} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {VocabularyModel} from "../vocabuiary-model";

@Component({
  selector: 'app-english-tech-add-new-word',
  templateUrl: './english-tech-add-new-word.component.html',
  styleUrls: ['./english-tech-add-new-word.component.css']
})
export class EnglishTechAddNewWordComponent implements OnInit {

  model = new VocabularyModel(null, null, null);
  wordOldInterprit = '';
  wordNewInterprit: String;

  constructor(private http: HttpClient) {
  }

  ngOnInit() {
  }

  iterpriteteWordRU_EN(value: string): string {


    var str = new String(value);
    var str_local = new String();
    console.log(str);
    console.log(str.length)

    for (let word of str.valueOf()) {
      console.log('word ' + word)

      if (word == 'q') word = 'й';
      if (word == 'w') word = 'ц';
      if (word == 'e') word = 'у';
      if (word == 'r') word = 'к';
      if (word == 't') word = 'е';
      if (word == 'y') word = 'н';
      if (word == 'u') word = 'г';
      if (word == 'i') word = 'ш';
      if (word == 'o') word = 'щ';
      if (word == 'p') word = 'з';
      if (word == '[') word = 'х';
      if (word == ']') word = 'ъ';
      if (word == 'a') word = 'ф';
      if (word == 's') word = 'ы';
      if (word == 'd') word = 'в';
      if (word == 'f') word = 'а';
      if (word == 'g') word = 'п';
      if (word == 'h') word = 'р';
      if (word == 'j') word = 'о';
      if (word == 'k') word = 'л';
      if (word == 'l') word = 'д';
      if (word == ';') word = 'ж';
      if (word == '\'') word = 'э';
      if (word == 'z') word = 'я';
      if (word == 'x') word = 'ч';
      if (word == 'c') word = 'с';
      if (word == 'v') word = 'м';
      if (word == 'b') word = 'и';
      if (word == 'n') word = 'т';
      if (word == 'm') word = 'ь';
      if (word == ',') word = 'б';
      if (word == '.') word = 'ю';
      if (word == '`') word = 'ё';



      console.log('word ' + word);

      str_local = str_local + word;

    }
    console.log('stroka lokal: '+str_local)
    return str_local.toString();
  }

  addNewWord(): void {
    console.log('add  new job')

    this.model.ruWord = this.iterpriteteWordRU_EN(this.model.ruWord);


    this.http.post('app/eng/tech/add/model', this.model)
      .subscribe();
    console.log('post: ' + this.model);
  }
}



