import {Component, OnInit} from '@angular/core';
import {HttpClient} from "@angular/common/http";

import {VocabularyModel} from "../vocabuiary-model";
import 'rxjs/add/operator/map'


import {Observable} from "rxjs/Observable";


@Component({
  selector: 'app-english-tech',
  templateUrl: './english-tech.component.html',
  styleUrls: ['./english-tech.component.css']
})

export class EnglishTechComponent implements OnInit {
  answer = '';
  answer2 = new VocabularyModel(1, '1', '2');

  showListSixExample: VocabularyModel[];
  showWordExample: VocabularyModel;
  min: number = 0;
  max: number = 5;
  rnd: number = null;
  color_text: object;

  model = new VocabularyModel(null, null, null);
  model2 = '';


  constructor(private http: HttpClient) {
  }

  ngOnInit() {
    this.http.get('/app/eng/tech/download/all/data', {responseType: 'text'})
      .subscribe(data => console.log(data));
    console.log("data")
  }




  getOneVocabuiaryModel() {
    this.http.get<VocabularyModel>('/get/model')
      .subscribe(data => {
        console.log('data: ' + data);
        this.answer2 = data;

      });
  }

  ShowListSixExample() {
    console.log('show list')
    this.min = 0;
    this.max = 5;
    this.rnd = this.min + Math.round(Math.random() * (this.max - this.min));
    console.log(this.rnd)
    this.http.get<VocabularyModel[]>('show/list/model/six')
      .subscribe(
        list => {
          this.showListSixExample = list;
          this.showWordExample = this.showListSixExample[this.rnd];
          this.color_text = {'color': 'blue'};
        }
      );

  }

  actionWord(wordActual: string): void {
    console.log(wordActual);
    console.log(this.showWordExample);
    if (this.showWordExample.enWord == wordActual) {
      console.log("Good " + wordActual);
      this.color_text = {'color': 'greenyellow'};


      setTimeout(() => {
        console.log('time 2000');
        this.ShowListSixExample();
      }, 2000);
    } else {
      console.log("Bad " + wordActual);
      this.color_text = {'color': 'red'};
    }
  }

  clearListWord():void{
    this.http.get('/app/eng/clear/list/word',{responseType:'text'})
      .subscribe(data=>console.log(data));
  }
}





