import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'formatRu'
})
export class FormatRuPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    let val=value.toUpperCase();
    return val;
  }

}



