import { NgModule } from '@angular/core';
import {RouterModule, Routes} from "@angular/router";
import {EnglishTechComponent} from "./english-tech/english-tech.component";
import {EnglishTechAddNewWordComponent} from "./english-tech-add-new-word/english-tech-add-new-word.component";
import {AppComponent} from "./app.component";

const routes:Routes=[
  {path:'home',redirectTo:'/',pathMatch:'full'},
  {path:'home',component:AppComponent},
  {path:'english_tech',component:EnglishTechComponent},
  {path:'english_tech_add',component:EnglishTechAddNewWordComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports:[RouterModule]

})
export class AppRoutingModule { }



