export class VocabularyModel {

  id: number;
  enWord: string;
  ruWord: string;

  constructor(id: number, enWord: string, ruWord: string) {
    this.id = id;
    this.enWord = enWord;
    this.ruWord = ruWord;
  }

  get _id(): number {
    return this.id;
  }

  set _id(value: number) {
    this.id = value;
  }

  get _enWord(): string {
    return this.enWord;
  }

  set _enWord(value: string) {
    this.enWord = value;
  }

  get _ruWord(): string {
    return this.ruWord;
  }

  set _ruWord(value: string) {
    this.ruWord = value;
  }


}



